@keyframes rotate
  0%
    transform: rotateX(0deg)
  50%
    transform: rotateX(-25deg)
  100%
    transform: rotateX(0deg)

@keyframes float
  0%
    transform: translateY(0) scale(1)
  50%
    transform: translateY(10px) scale(1.1)
  100%
    transform: translateY(0) scale(1)

@keyframes float-reverse
  0%
    transform: translateY(0) scale(1)
  50%
    transform: translateY(10px) scale(1.1)
  100%
    transform: translateY(0) scale(1)

@keyframes star-animation
  0%
    transform: translateY(0) rotate(0deg) scale(1)
  50%
    transform: translateY(-25px) rotate(10deg) scale(1.3) 
  100%
    transform: translateY(0) rotate(0deg) scale(1) 

@keyframes star-animation-reverse
  0%
    transform: translateY(0) rotate(0deg) scale(1) 
  50%
    transform: translateY(25px) rotate(-10deg) scale(1.2) 
  100%
    transform: translateY(0) rotate(0deg) scale(1)

@keyframes social 
  0%
    transform: translate(-45px, 0px) scale(.8) 
  50%
    transform: translate(-10px, -10px) scale(.9)
  100%
    transform: translate(-45px, 0px) scale(.8)

@keyframes like
  0%
    transform: translate(-40px,10px) scale(.8) 
  50%
    transform: translate(-10px,0px) scale(.9)
  100%
    transform: translate(-40px,10px) scale(.8)

@keyframes location
  0%
    transform: translate(-40px,-35px) scale(.8) 
  50%
    transform: translate(-10px,0px) scale(.9)
  100%
    transform: translate(-40px,-35px) scale(.8)

@keyframes microphone 
  0%
    transform: translate(0) rotate(-5deg)
  50%
    transform: rotate(0deg) 
  100%
    transform: translate(0) rotate(-5deg)

