@keyframes contactImg
    0%
        transform: rotate(0deg)
    100%
        transform: rotate(360deg)

@keyframes star-animation
  0%
    transform: translateY(0) rotate(0deg) scale(1)
  50%
    transform: translate(10px,10px) rotate(8deg) scale(1.1) 
  100%
    transform: translateY(0) rotate(0deg) scale(1) 